body,
* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    font-family: sans-serif;
    font-weight: bold;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: rgb(27, 27, 27);
}

h1 {
    font-size: 76px;
    letter-spacing: 16px;
    text-align: center;
    position: relative;
    right: -6px;
    color: rgb(230, 230, 230);
    height: fit-content;
    line-height: 90%;
    margin-bottom: 10px;
}

.game-description {
    color: rgb(230, 230, 230);
    font-size: 18px;
}

.game-container {
    display: grid;
    grid-template-rows: repeat(6, 60px);
    grid-template-columns: repeat(4, 60px);
    gap: 10px;
}

.cell {
    width: 60px;
    height: 60px;
    border: 4px solid rgb(61, 61, 61);
    font-size: 32px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(230, 230, 230);
}

.cell.right {
    background-color: rgb(19, 129, 19);
    border: 4px solid rgb(19, 129, 19);
}

.cell.close {
    background-color: rgb(175, 175, 9);
    border: 4px solid rgb(175, 175, 9);
}

.cell.wrong {
    background-color: rgb(61, 61, 61);
}

.keyboard-container .right {
    background-color: rgb(19, 129, 19);
}

.keyboard-container .close {
    background-color: rgb(175, 175, 9);
}

.keyboard-container .wrong {
    background-color: rgb(61, 61, 61);
}



.new-game {
    margin: 10px 0px 30px;
    font-size: 14px;
    color: rgb(230, 230, 230);
    font-weight: bold;
    visibility: hidden;
}


.answer-display {
    padding: 8px 16px;
    background-color: whitesmoke;
    color: rgb(27, 27, 27);
    border-radius: 8px;
    margin: 30px 0px 10px;
    visibility: hidden;
    height: 32px;
    font-size: 16px;
    align-items: center;
    display: flex;
}

.shake-horizontal {
    animation: shakeHorizontal 0.5s ease-in-out 2; 
  }
  
  @keyframes shakeHorizontal {
    0% { transform: translateX(0); }
    10% { transform: translateX(-5px); }
    30% { transform: translateX(5px); }
    50% { transform: translateX(-5px); }
    70% { transform: translateX(5px); }
    90% { transform: translateX(-5px); }
    100% { transform: translateX(0); }
  }


.keyboard-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 7px;
}

.row {
    display: flex;
    gap: 6px;
}

.keyboard-container .enter,
.keyboard-container .delete {
    width: 55px;
    font-size: 12px;
}

.keyboard-container button {
    font-size: 20px;
    background-color: rgb(134, 134, 134);
    height: 50px;
    width: 35px;
    border-radius: 4px;
    color: rgb(230, 230, 230)
}

.keyboard-container:hover {
    cursor: pointer;
}

@media only screen and (max-width: 480px) {

    html,
    body {
        width: 100%;
        margin: 0;
        height: 100%;
    }

    h1 {
        font-size: 3.5rem;
    }

    .game-description {
        font-size: .9rem;
    }

    .keyboard-container {
        width: 100%;
        height: 25%;
    }

    .row {
        width: 100%;
        height: 33%;
        display: flex;
        justify-content: center;
    }

    .row button {
        width: 8%;
        height: 100%;
    }

    .row .enter, 
    .row .delete {
        font-size: .65rem;
        width: 13%;
    }

    .game-container {
        grid-template-rows: repeat(6, 3.2rem);
        grid-template-columns: repeat(4, 3.2rem);
        gap: .5rem;    
    }

    .cell {
        width: 3.2rem;
        height: 3.2rem;
    }

    .answer-display {
        margin-top: 1rem;
        font-size: .8rem;
        height: 1.1rem;
        padding: .6rem .7rem;
        border-radius: 6px;
    }

    .new-game {
        font-size: .8rem;
        margin-bottom: 1.5rem;
    }

}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;IAGI,SAAS;IACT,UAAU;IACV,SAAS;IACT,sBAAsB;IACtB,uBAAuB;IACvB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;IACb,iCAAiC;AACrC;;AAEA;IACI,eAAe;IACf,oBAAoB;IACpB,kBAAkB;IAClB,kBAAkB;IAClB,WAAW;IACX,yBAAyB;IACzB,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mCAAmC;IACnC,sCAAsC;IACtC,SAAS;AACb;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,iCAAiC;IACjC,eAAe;IACf,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;AAC7B;;AAEA;IACI,kCAAkC;IAClC,kCAAkC;AACtC;;AAEA;IACI,kCAAkC;IAClC,kCAAkC;AACtC;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,iCAAiC;AACrC;;;;AAIA;IACI,qBAAqB;IACrB,eAAe;IACf,yBAAyB;IACzB,iBAAiB;IACjB,kBAAkB;AACtB;;;AAGA;IACI,iBAAiB;IACjB,4BAA4B;IAC5B,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;IACrB,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,6CAA6C;EAC/C;;EAEA;IACE,KAAK,wBAAwB,EAAE;IAC/B,MAAM,2BAA2B,EAAE;IACnC,MAAM,0BAA0B,EAAE;IAClC,MAAM,2BAA2B,EAAE;IACnC,MAAM,0BAA0B,EAAE;IAClC,MAAM,2BAA2B,EAAE;IACnC,OAAO,wBAAwB,EAAE;EACnC;;;AAGF;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;;IAEI,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,oCAAoC;IACpC,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB;AACJ;;AAEA;IACI,eAAe;AACnB;;AAEA;;IAEI;;QAEI,WAAW;QACX,SAAS;QACT,YAAY;IAChB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,WAAW;QACX,WAAW;IACf;;IAEA;QACI,WAAW;QACX,WAAW;QACX,aAAa;QACb,uBAAuB;IAC3B;;IAEA;QACI,SAAS;QACT,YAAY;IAChB;;IAEA;;QAEI,iBAAiB;QACjB,UAAU;IACd;;IAEA;QACI,qCAAqC;QACrC,wCAAwC;QACxC,UAAU;IACd;;IAEA;QACI,aAAa;QACb,cAAc;IAClB;;IAEA;QACI,gBAAgB;QAChB,gBAAgB;QAChB,cAAc;QACd,oBAAoB;QACpB,kBAAkB;IACtB;;IAEA;QACI,gBAAgB;QAChB,qBAAqB;IACzB;;AAEJ","sourcesContent":["html,\nbody,\n* {\n    margin: 0;\n    padding: 0;\n    border: 0;\n    box-sizing: border-box;\n    font-family: sans-serif;\n    font-weight: bold;\n}\n\nbody {\n   